a { color: inherit; }

a:hover { text-decoration: none; }

img { width: 100%; }

header .head-container { display: flex; flex-direction: column; padding: 0; }

header .head { display: flex; width: 100%; justify-content: space-around; height: 70px; padding: 0 25px 0 25px; }

header .head .logo { width: 50%; display: flex; align-items: center; }

header .head .logo h2 { font-family: 'Roboto'; font-size: 32px; }

header .head .top-box { width: 50%; display: flex; justify-content: flex-end; align-items: center; }

header .head .top-box h2 { font-family: 'Roboto'; font-size: 22px; }

header .under-head { background: #efefef; display: flex; width: 100%; }

header .under-head .ul-menu { height: 60px; width: 1280px; display: flex; flex-direction: row; align-items: center; justify-content: space-around; }

header .under-head .ul-menu .items { display: flex; align-items: center; font-family: 'Roboto'; font-size: 18px; font-weight: 500; color: #37474f; }

header .under-head .ul-menu .items .btn-secondary.dropdown-toggle { background: inherit; border: 0; box-shadow: none; color: #37474f; }

header .under-head .ul-menu .items .btn-secondary.dropdown-toggle:after { display: none; }

#main { background-size: cover; background-position: center; background-repeat: no-repeat; }

#main h1 { font-family: 'Roboto'; font-size: 22px; font-weight: 500; color: #37474f; margin: 20px 0 20px 0; }

#main .table-container { background: #ffffff; display: flex; flex-direction: column; position: relative; }

#main .table-container .table { font-family: 'Roboto'; font-size: 16px; font-weight: 500; color: #37474f; }

#main .table-container .tables .mini-forms { display: flex; }

#main .table-container .tables .mini-forms form { width: 90%; }

#main .table-container .tables .mini-forms .checkboxbox { width: 15%; display: flex; align-items: center; justify-content: center; }

#main .table-container .tables .mini-forms .checkboxbox h3 { font-family: 'Roboto'; font-size: 16px; font-weight: 500; color: #37474f; }

#main .table-container .tables .mini-forms .checkboxbox .checkbox { -webkit-appearance: checkbox; width: 20px; height: 20px; margin-right: 5px; }

#main .table-container .tables #form1 { max-width: 700px; padding: 2rem; box-sizing: border-box; }

#main .table-container .tables #form1 .form-field { display: flex; margin: 0 0 1rem 0; justify-content: center; align-items: center; font-family: 'Roboto'; font-weight: 400; font-size: 16px; }

#main .table-container .tables #form1 .form-field .checkbox { -webkit-appearance: checkbox; width: 20px; height: 20px; margin-left: 14%; }

#main .table-container .tables #form1 .form-field .checkbox-label { width: 50%; }

#main .table-container .tables #form1 label, #main .table-container .tables #form1 input { width: 70%; padding: 0.5rem; box-sizing: border-box; justify-content: space-between; font-size: 1.1rem; }

#main .table-container .tables #form1 label { text-align: right; width: 30%; }

#main .table-container .tables #form1 input { border: 2px solid #aaa; border-radius: 2px; }

#main .table-container .second-column { width: 40px; padding: 0; }

#main .left-content { padding: 10px; display: flex; width: 100%; }

#main .left-content .left-menu-items { display: flex; align-items: center; font-family: 'Roboto'; font-size: 18px; font-weight: 500; color: #37474f; }

#main .right-content { width: 100%; padding: 10px; }

#main .right-content .box { width: 100%; height: 600px; }

#main .main-container-editing { position: relative; background: #ffffff; height: 100%; padding-bottom: 100px; display: flex; flex-direction: column; }

#main #form { max-width: 700px; padding: 2rem; box-sizing: border-box; }

#main #form .form-field { display: flex; margin: 0 0 1rem 0; justify-content: center; align-items: center; font-family: 'Roboto'; font-weight: 400; font-size: 16px; }

#main #form .form-field .checkbox { -webkit-appearance: checkbox; width: 20px; height: 20px; margin-left: 14%; }

#main #form .form-field .checkbox-label { width: 50%; }

#main #form label, #main #form input { width: 70%; padding: 0.5rem; box-sizing: border-box; justify-content: space-between; font-size: 1.1rem; }

#main #form label { text-align: right; width: 30%; }

#main #form input { border: 2px solid #aaa; border-radius: 2px; }

#main .form-box { width: 100%; display: flex; flex-direction: column; }

#main .icons { display: flex; }

#main .icons .client-icon { width: 74%; }

#main .icons .change-icons { display: flex; width: 18%; }

#main .icons .change-icons ul { display: flex; flex-direction: column; justify-content: flex-end; padding-bottom: 23%; }

#main .buttons { display: flex; justify-content: center; }

#main .buttons .buttons-client { background: #DCDCDC; height: 40px; width: 200px; margin: 0 20px 0 20px; display: flex; align-items: center; justify-content: center; border-radius: 3px; border: 1px solid #6c757d; font-family: 'Roboto'; font-weight: 400; font-size: 16px; }

#main .instruments { display: flex; width: 100%; position: absolute; bottom: 0; }

#main .instruments .instruments-ul { width: 100%; display: flex; justify-content: center; }

#main .instruments .instruments-ul li { width: 15%; }

#main .instruments .instruments-ul li .instrument { width: 100%; border: 1px solid #4e555b; height: 22px; }

#main .instruments .instruments-ul li .instrument:hover { cursor: pointer; }

footer .footer { height: 60px; display: flex; width: 100%; justify-content: center; align-items: center; background: #efefef; }
